import { Component, OnInit } from '@angular/core';
import { FileService } from '../../service/file.service';
import {saveAs} from 'file-saver';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-download-sample',
  templateUrl: './download-sample.component.html',
  styleUrls: ['./download-sample.component.scss']
})
export class DownloadSampleComponent implements OnInit {

  constructor(private fileService:FileService, private spinner:NgxSpinnerService) { }

  ngOnInit(): void {
  }

  downloadFile(){
    this.spinner.show();
    this.fileService.downloadFile().subscribe(blob=>saveAs(blob,"Sample"+".xlsx"));
    console.log("HEE");
    
    this.spinner.hide();
  }
}
