import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { NgxSpinner } from 'ngx-spinner/lib/ngx-spinner.enum';
import { LoginRequest } from '../model/file.model';
import { FileService } from '../service/file.service';

@Component({
  selector: 'app-login-pulse',
  templateUrl: './login-pulse.component.html',
  styleUrls: ['./login-pulse.component.scss']
})
export class LoginPulseComponent implements OnInit {

  constructor(private spinner: NgxSpinnerService ,private fileService:FileService) { }

  loginDetails:LoginRequest=new LoginRequest();
  ngOnInit(): void {
  }

  doLogin(){
    this.spinner.show();
    if((this.loginDetails.username!='' && this.loginDetails.password!='')&& (this.loginDetails.username!=null && this.loginDetails.password !=null)) {
      console.log("Login Called");
      
          this.fileService.login(this.loginDetails).subscribe(
            (response:any) =>{
                  console.log(response);
                  this.fileService.loginUser(response);
                  window.location.href="/#/show";
            }, error =>{
                console.error(error);
                
            }
          )
    }
   
    
    this.spinner.hide();
  }
}
