import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Import Containers
import { DefaultLayoutComponent } from './containers';
import { DownloadSampleComponent } from './file/download-sample/download-sample.component';
import { FileListComponent } from './file/file-list/file-list.component';
import { HomeComponent } from './file/home/home.component';
import { InsertRowComponent } from './file/insert-row/insert-row.component';
import { UpdateFileComponent } from './file/update-file/update-file.component';
import { ViewComponent } from './file/view/view.component';
import { ForgetPasswordComponent } from './forget-password/forget-password.component';
import { LoginPulseComponent } from './login-pulse/login-pulse.component';
import { AuthGuard } from './service/auth.guard';

import { P404Component } from './views/error/404.component';
import { P500Component } from './views/error/500.component';
// import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './views/register/register.component';

export const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full',
  },
  {
    path: 'login',
    component: LoginPulseComponent,
    data: {
      title: 'Login Page'
    }
  },
  {
    path: 'forgetpassword',
    component: ForgetPasswordComponent,
    data: {
      title: 'Forget Page'
    }
  },
  {
    path: 'register',
    component: RegisterComponent,
    data: {
      title: 'Register Page'
    }
  },
  {
    path: '',
    component: DefaultLayoutComponent,
    data: {
      title: 'Home'
    },
    children: [
      // {
      //   path: 'dashboard',
      //   loadChildren: () => import('./views/dashboard/dashboard.module').then(m => m.DashboardModule)
      // },
      {
        path: 'show',
       component: FileListComponent
      },
      {
        path: 'add',
       component: InsertRowComponent
      },
      {
        path: 'home',
       component: HomeComponent
      },
      {
        path: 'view/:srNo',
       component: ViewComponent
      },
      {
        path: 'update/:srNo',
       component: UpdateFileComponent
      },
      {
          path: 'downloadSample',
          component: DownloadSampleComponent
      }
    ]
  },
  { path: '**', component: P404Component }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' }) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
