import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { FileRequest } from '../../model/file.model';
import { FileService } from '../../service/file.service';

@Component({
  selector: 'app-insert-row',
  templateUrl: './insert-row.component.html',
  styleUrls: ['./insert-row.component.scss']
})
export class InsertRowComponent implements OnInit {

  file: FileRequest = new FileRequest();

  constructor(private router: Router, private fileServices: FileService, private spinner:NgxSpinnerService) { }

  ngOnInit(): void { }

  doSubmitForm() {
    this.spinner.show();
    this.fileServices.createRow(this.file).subscribe(
      response => {
        console.log(response);
      },
      error => {
        console.log(error);
      });
      this.spinner.hide();
    this.router.navigate(['/show']);
  }
}

