import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { FileRequest } from '../../model/file.model';
import { FileService } from '../../service/file.service';

@Component({
  selector: 'app-update-file',
  templateUrl: './update-file.component.html',
  styleUrls: ['./update-file.component.scss']
})
export class UpdateFileComponent implements OnInit {

  srNo:number;
  file:any= new FileRequest();


  constructor(private fileService:FileService ,private router:Router,private route:ActivatedRoute, private spinner:NgxSpinnerService) { }

  ngOnInit() {
    this.file=new FileRequest();
    this.srNo=this.route.snapshot.params['srNo'];
    this.fileService.getFileById(this.srNo).subscribe(
      data=>{
        console.log(JSON.stringify(data));
        this.file=data;
    },
      error=>console.log(error));
  }

  onSubmit() {
    this.spinner.show();
    this.fileService.updateRow(this.srNo,this.file).subscribe(
      data=>console.log(data),error=>console.error());
      // this.fileService.getFile();'
      this.spinner.hide();
      this.router.navigate(['/show']);
      
  }

  onCancel(){
    this.router.navigate(['/show']);
  }
  

}