import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { FileRequest } from '../../model/file.model';
import { FileService } from '../../service/file.service';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {
  srNo:number;
  file:any= new FileRequest();
  
  constructor(private fileService:FileService ,private router:Router,private route:ActivatedRoute, private spinner:NgxSpinnerService) { }

  ngOnInit(): void {
    this.file=new FileRequest();
    this.srNo=this.route.snapshot.params['srNo'];
    this.fileService.getFileById(this.srNo).subscribe(
      data=>{
        console.log(JSON.stringify(data));
        this.file=data;
    },
      error=>console.log(error));
  }

  updateItem(srNo:number){
    this.spinner.show();
    this.router.navigate(['update', srNo]);
    this.spinner.hide();
  }
  onCancel(){
    this.spinner.show();
    this.router.navigate(['/show']);
    this.spinner.hide();
  }

}
