import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FileService } from '../../service/file.service';
import { NgxSpinnerService } from 'ngx-spinner';
import {saveAs} from 'file-saver';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  message:string="";
  loading: boolean = false;
  file: File;

  constructor(private fileService:FileService ,private router:Router,private spinner:NgxSpinnerService) { }

  ngOnInit(): void {
    
  }

  downloadFile(){
    this.spinner.show();
    this.fileService.downloadFile().subscribe(blob=>saveAs(blob,"Sample"+".xlsx"));
  }

  onChange(event:any) {
    this.file = event.target.files[0];
  }

  onUpload() {
    this.spinner.show();
    if(this.file!=null){
    this.loading = true;
    console.log(this.file);
    this.fileService.saveFile(this.file).subscribe(
        (event: any) => {
            if (typeof (event) === 'object') {
              this.spinner.hide();
                this.router.navigate(['/show'])
                this.loading = false;
            }
        }
    );
  }
     
}

}